@use 'styles/utils' as utils;

.checkbox {
    @include utils.set-size(16px);
    @include utils.set-transition((border-color, background-color, color));
    @include utils.flex-center;

    border: 2px transparent solid;
    border-radius: 4px;
    flex-shrink: 0;
    color: #ffffff;
    padding: 0;
}

.checked {
    border-color: #0029ff;
    background: #0029ff;
}

.unchecked {
    border-color: #64748b;
}
