@use 'styles/utils' as utils;

.wrapper {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 16px;
    line-height: 24px;

    svg {
        @include utils.set-size(20px);
    }
}

.statusInfo {
    color: #94a3b8;
}

.statusSuccess {
    color: #10b981;
}

.statusWarning {
    color: #f59e0b;
}

.statusError {
    color: #ec4899;
}
