@use 'styles/variables' as vars;

.tabWrapper {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e5e7eb;
}

.tabButton {
    position: relative;
    display: grid;
    grid-template-columns: minmax(20px, auto) auto;
    align-items: center;
    gap: 8px;
    height: 48px;
    padding: 0 24px;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    transition:
        color ease-in-out 0.2s,
        background-color ease-in-out 0.2s;

    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 2px;
        background: transparent;
        transition: background-color ease-in-out 0.2s;
    }
}

.buttonDefault {
    color: vars.$color-type-high-contrast;
    border-bottom-color: transparent;
}

.buttonActive {
    color: #0029ff;

    &::after {
        background: #0029ff;
    }
}
