@use 'styles/utils' as utils;

.buttonIcon {
    @include utils.set-transition((color, background-color), 0.2s, easy-in-out);
    @include utils.flex-center;
}

.sizeSmall {
    @include utils.set-size(32px);

    svg,
    img {
        @include utils.set-size(16px);
    }
}

.sizeBase {
    @include utils.set-size(40px);
}

.sizeLarge {
    @include utils.set-size(48px);
}

.sizeBase,
.sizeLarge {
    svg,
    img {
        @include utils.set-size(20px);
    }
}
