@use 'styles/variables' as vars;
@use 'styles/utils' as utils;
@use 'styles/media' as media;

.tooltip {
    --width: 308px;

    background-color: vars.$color-components-tooltips-bg;
    border-radius: 4px;
    color: vars.$color-components-tooltips-text;
    display: none;
    font-size: utils.to-rem(14px);
    text-align: center;
    width: var(--width);
    padding: 16px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    cursor: default;
    z-index: vars.$z-index-tooltip;

    &.isTop {
        bottom: calc(100% + 12px);

        &:before {
            bottom: -6px;
            clip-path: polygon(50% 100%, 0 0, 100% 0);
        }
    }

    &.isBottom {
        top: calc(100% + 10px);

        &:before {
            top: -6px;
            clip-path: polygon(50% 0, 0% 100%, 100% 100%);
        }
    }

    &:before {
        background-color: vars.$color-components-tooltips-bg;
        content: '';
        display: block;
        height: 6px;
        width: 14px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
    }
}

.root {
    position: relative;
    z-index: 1;

    @include media.no-touch-hover {
        .tooltip {
            display: block;
        }
    }

    &:focus,
    &:focus-visible,
    &:focus-within {
        .tooltip {
            display: block;
        }
    }
}
