@use 'styles/utils' as utils;
@use 'styles/media' as media;
@use 'styles/variables' as vars;

.buttonLoader {
    position: relative;
    top: 2px;
    width: 20px;
}

.buttonSizeLarge {
    height: 48px;
    padding: 0 24px;
}

.buttonSizeBase {
    height: 40px;
    padding: 0 20px;
}

.buttonSizeSmall {
    height: 32px;
    padding: 0 16px;
}

.buttonPrimaryColor {
    color: #ffffff;
    background: #0029ff;

    @include media.no-touch-hover {
        background: #0434b1;
    }
}

.buttonSecondaryColor {
    color: #0029ff;
    background: #f4f9ff;

    @include media.no-touch-hover {
        color: #2563eb;
        background: #dbeafe;
    }
}

.buttonOutlineColor {
    border: 1px solid vars.$color-primary-500;
    color: #0029ff;

    @include media.no-touch-hover {
        border-color: #0434b1;
        color: #0434b1;
    }
}

.buttonLinkColor {
    color: vars.$color-primary-500;

    @include media.no-touch-hover {
        color: #0434b1;
        background: #f4f9ff;
    }
}

.buttonGrayColor {
    color: #ffffff;
    background: #64748b;

    &:hover {
        background: #4b5563;
    }
}

.buttonGraySecondaryColor {
    color: #64748b;

    &:hover {
        color: #4b5563;
    }
}

.button {
    @include utils.set-transition((color, background-color, border-color));
    @include utils.flex-center;

    gap: 8px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
}

.buttonIconWrapper {
    @include utils.set-size(20px);
}
