@use 'styles/variables' as vars;
@use 'styles/utils' as utils;
@use 'styles/media' as media;

.avatarText {
    display: grid;
    grid-template-columns: 48px auto 20px;
    gap: 12px;
    align-items: center;
    margin: 0;
    padding: 8px;
    border-radius: 8px;
}

.avatarTextCaption {
    display: flex;
    flex-direction: column;
    gap: 4px;
    overflow: hidden;
}

.avatarTextName {
    font-size: 18px;
    font-weight: 500;
    line-height: 28px;
    color: vars.$color-type-high-contrast;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.avatarTextSubtext {
    font-size: 12px;
    line-height: 16px;
}

.subtextDefault {
    color: vars.$color-type-low-contrast;
}

.subtextWarning {
    color: #f59e0b;
}

.actionsRow {
    align-content: center;
    display: flex;
}

.linkIcon,
.buttonIcon {
    @include utils.set-size(32px);
    @include utils.flex-center;
    @include utils.set-transition(background-color);

    color: vars.$color-primary-500;
    background-color: transparent;
    flex-shrink: 0;
    padding: 8px;

    @include media.no-touch-hover {
        background-color: vars.$color-50;
    }

    svg {
        flex-shrink: 0;
    }
}

.tooltipContainer {
    align-self: self-start;
}

.tooltip {
    width: 192px;
}

.usersIcon {
    color: vars.$color-type-low-contrast;
}
