@use 'styles/variables' as vars;
@use 'styles/utils' as utils;
@use 'styles/media' as media;
@use 'styles/elements/modal' as modal;
@use 'styles/elements/inputs' as inputs;

$upload-button-width: 116px;
$gap: 16px;

.root {
  @include modal.set-max-width(700px);

  z-index: vars.$z-index-help-modal;
}

.description {
  @include modal.text;
  width: auto !important;
  resize: vertical;
  max-height: 400px;
}

.fieldset {
  display: flex;
  flex-direction: column;
  gap: $gap;
  margin-top: 24px;

  @include media.vw-small-down {
    gap: 8px;
    margin-top: 16px;
  }
}

.theme,
.description,
.name,
.email {
  color: vars.$color-type-base-contrast;
}

.uploadWrapper {
  border: 1px solid vars.$color-neutral-200;
  border-radius: 4px;
  display: flex;
  position: relative;
  overflow-x: hidden;

  &.isError {
    border-color: vars.$color-semantic-error-500;
  }
}

.upload {
  flex-shrink: 0;
  font-size: utils.to-rem(12px);
  width: $upload-button-width;
}

.fileName {
  @include utils.line-clamp(1);

  color: vars.$color-type-base-contrast;
  font-size: utils.to-rem(12px);
  font-weight: 500;
  position: absolute;
  left: calc($upload-button-width + 8px);
  top: 50%;
  transform: translateY(-50%);
  max-width: calc(100% - $upload-button-width - 40px);
}

.deleteFileButton {
  @include utils.set-size(20px);
  @include utils.set-transition(opacity);

  color: vars.$color-type-base-contrast;
  flex-shrink: 0;
  margin: auto 8px auto auto;
  padding: 0;

  @include media.no-touch-hover {
    opacity: 0.8;
  }
}

.uploadIcon {
  display: none;
}

.webInfoBlock {
  display: flex;
  gap: 8px;
  margin-top: 24px;
}

.webInfoCheckbox {
  margin-top: 4px;
}

.webInfoText {
  margin-top: 0;
  color: vars.$color-type-base-contrast;
  font-size: utils.to-rem(16px);
  line-height: 1.5;

  @include media.vw-small-down {
    font-size: utils.to-rem(14px);
  }
}

.webInfoQuestion {
  @include utils.set-transition(opacity);

  color: vars.$color-500;
  font-size: utils.to-rem(12px);
  padding-block: 8px;
  opacity: 1;

  @include media.no-touch-hover {
    opacity: 0.7;
  }
}

.webInfo {
  background-color: vars.$color-foundation-background;
  border: 1px solid vars.$color-foundation-divider;
  border-radius: 8px;
  color: vars.$color-type-base-contrast;
  font-size: utils.to-rem(12px);
  padding: 12px;
}

.actions {
  @include modal.actions-block;

  border-top: 1px solid vars.$color-foundation-divider;
  padding-top: 24px;
  width: 100%;

  @include media.vw-small-down {
    margin-top: 16px;
  }
}

.errorText {
  @include inputs.subtext;
  @include inputs.subtext-error;
}
