@use 'styles/media' as media;
@use 'styles/utils' as utils;
@use 'styles/variables' as vars;
@use 'styles/elements/inputs' as inputs;

.root {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
}

.label {
    @include inputs.label;

    &.isLabelFloated {
        @include inputs.floated-label(true);

        &.isError {
            color: vars.$color-semantic-error-500;
        }
    }
}

.input {
    @include utils.set-transition(border-color);

    color: vars.$color-type-base-contrast;
    line-height: 1.2;
    appearance: none;
    background-color: white;
    border: 1px solid vars.$color-components-textareas-border;
    border-radius: 4px;
    font-size: utils.to-rem(14px);
    padding: 8px 16px;
    min-height: 96px;
    width: 100%;

    &.isError {
        border-color: vars.$color-semantic-error-500;
    }

    &:focus,
    &:active {
        border-color: vars.$color-primary-500;
        outline: none;

        & + .label {
            color: vars.$color-primary-500;
        }

        &.isError {
            border-color: vars.$color-semantic-error-500;

            & + .label {
                color: vars.$color-semantic-error-500;
            }
        }
    }
}

.subText {
    @include inputs.subtext;

    &.isError {
        @include inputs.subtext-error;
    }
}
