@use 'styles/variables' as vars;
@use 'styles/utils' as utils;
@use 'styles/elements/inputs' as inputs;

.root {
    position: relative;
}

.labelText {
    @include utils.line-clamp(1, 1.5);
}

.labelWrapper {
    @include inputs.label;

    display: flex;
    font-size: utils.to-rem(14px);

    &.isLarge {
        font-size: utils.to-rem(16px);
        top: 14px;
    }

    &.isLabelFloated {
        @include inputs.floated-label;

        &.isError {
            color: vars.$color-semantic-error-500;
        }
    }
}

.subText {
    padding: 4px 16px 0;
    font-size: 12px;
    line-height: 16px;
    color: vars.$color-type-low-contrast;
    pointer-events: none;

    &.isError {
        color: vars.$color-semantic-error-500;
    }
}

.inputIconWrapper {
    @include utils.set-size(20px);

    svg,
    img {
        width: 20px;
    }
}

.sizeBase {
    height: 40px;
    padding: 0 16px;

    input {
        font-size: 14px;
        line-height: 20px;
    }
}

.sizeLarge {
    height: 48px;
    padding: 0 16px;

    input {
        font-size: 16px;
        line-height: 24px;
    }
}

.disabled {
    display: flex;
    background: vars.$color-components-input-groups-disabled-background;

    input {
        background: vars.$color-components-input-groups-disabled-background;
    }
}

.wrapper {
    display: flex;
    align-items: center;
    align-self: stretch;
    gap: 8px;
    border-radius: 4px;
    border: 1px solid #e2e8f0;
    transition:
        color ease-in-out 0.2s,
        background-color ease-in-out 0.2s,
        border ease-in-out 0.2s;
    background: #fff;
    position: relative;

    input {
        width: 100%;
        margin: 0;
        padding: 0;
        border: none;
        outline: none;

        &:-webkit-autofill {
            &:hover,
            &:focus {
                // stylelint-disable declaration-no-important
                -webkit-box-shadow: 0 0 0 1000px white inset !important;
                // stylelint-enable declaration-no-important
            }
        }
    }
}

.statusError {
    border-color: #ec4899;

    .labelWrapper {
        &.isLabelFloated {
            color: vars.$color-semantic-error-500;
        }
    }
}

.statusDefault {
    &:focus-within {
        border-color: vars.$color-primary-500;

        .labelWrapper {
            &.isLabelFloated {
                color: vars.$color-primary-500;
            }
        }
    }
}

.loader {
    color: #0029ff;
}
