@use 'styles/utils' as utils;
@use 'styles/variables' as vars;
@use 'styles/media' as media;

.root {
    border-radius: 4px;
    display: flex;
    gap: 16px;
    padding: 12px 16px;

    &.isYellow {
        background-color: vars.$color-semantic-warning-50;
        border: 1px solid vars.$color-semantic-warning-100;
        color: vars.$color-semantic-warning-500;
    }

    &.isRed {
        background-color: vars.$color-semantic-error-50;
        border: 1px solid vars.$color-semantic-error-100;
        color: vars.$color-semantic-error-500;
    }

    &.isGray {
        background-color: vars.$color-neutral-50;
        border: 1px solid vars.$color-neutral-100;
        color: vars.$color-neutral-500;
    }
}

.icon {
    @include utils.set-size(20px);

    min-width: 20px;
}

.label,
.text {
    font-size: utils.to-rem(14px);
    line-height: 1.4;
}

.label {
    font-weight: 600;
}

.text {
    margin-top: 8px;
}

.button {
    @include utils.set-size(32px);
    @include utils.set-transition(opacity);
    @include utils.flex-center;

    flex-shrink: 0;
    padding: 8px;
    margin-left: auto;
    opacity: 1;

    @include media.no-touch-hover {
        opacity: 0.7;
    }

    &.noText {
        @include utils.set-size(inherit);

        padding: 0;
    }

    & > svg {
        @include utils.set-size(16px);
    }
}
