@use 'styles/utils' as utils;
@use 'styles/variables' as vars;

@mixin set-max-width($max-width) {
    $padding-x: 8px;

    max-width: $max-width;

    @media (max-width: $max-width + $padding-x + $padding-x) {
        max-width: calc(100% - $padding-x - $padding-x);
    }
}

@mixin text {
    color: vars.$color-type-base-contrast;
    font-size: utils.to-rem(14px);
    font-weight: 400;
    margin-top: 4px;
}

@mixin actions-block {
    align-items: center;
    display: inline-flex;
    gap: 16px;
    margin-top: 24px;

    button {
        width: fit-content;
    }
}
