@use 'styles/utils' as utils;
@use 'styles/variables' as vars;

.avatar {
    position: relative;
}

.sizeLarge {
    @include utils.set-size(48px);
}

.sizeBase {
    @include utils.set-size(40px);
}

.sizeSmall {
    @include utils.set-size(32px);
}

.avatarWrapper {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    position: relative;

    img {
        @include utils.full-size-image();
    }
}

.avatarSmallIcon {
    position: absolute;
    right: -8px;
    top: -4px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    padding: 2px;
    border: 2px solid #fff;
    border-radius: 50%;
    color: #ffffff;
    background: vars.$color-type-high-contrast;

    svg {
        @include utils.set-size(16px);

        display: block;
    }
}

.imageStub {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-size: 20px;
    line-height: 1;
    color: #ffffff;
    background: #0029ff;
}
