@use 'styles/utils' as utils;
@use 'styles/media' as media;
@use 'styles/variables' as vars;
@use 'styles/elements/modal' as modal;

.root {
    @include modal.set-max-width(500px);

    background-color: vars.$color-foundation-surface;
    border-radius: 4px;
    padding: 24px;
    width: 100%;

    &::backdrop {
        background-color: vars.$color-components-modals-backdrop;
        backdrop-filter: blur(2px);
    }
}

.header {
    align-items: center;
    display: flex;
    justify-content: space-between;
    gap: 24px;
}

.title {
    color: vars.$color-type-high-contrast;
    font-size: utils.to-rem(20px);
    font-weight: 500;
}

.closeButton {
    @include utils.set-size(40px);
    @include utils.flex-center;
    @include utils.set-transition(background-color);

    background-color: transparent;
    border-radius: 4px;
    border: none;
    outline: none;
    color: vars.$color-primary-500;
    padding: 10px;

    @include media.no-touch-hover {
        background-color: vars.$color-50;
    }
}

.closeIcon {
    @include utils.set-size(20px);

    flex-shrink: 0;
}
