@use 'styles/media' as media;
@use 'styles/utils' as utils;
@use 'styles/variables' as vars;

$toggler-size: 16px;

.toggleSection {
    align-items: center;
    display: flex;
    gap: 8px;
}

.button {
    @include utils.set-transition(background-color);

    background-color: vars.$color-components-toggles-bg;
    border-radius: 100px;
    height: $toggler-size;
    width: 32px;
    flex-shrink: 0;

    &:before {
        @include utils.set-size($toggler-size);
        @include utils.set-transition((left, background-color));

        background-color: vars.$color-components-toggles-border;
        border-radius: 100px;
        content: '';
        display: block;
        position: relative;
        left: 0;
    }

    &:disabled {
        background-color: vars.$color-neutral-200;

        &:before {
            background-color: vars.$color-neutral-500;
        }
    }

    &.isActive {
        background-color: vars.$color-primary-200;

        &:before {
            background-color: vars.$color-primary-500;
            left: calc(100% - $toggler-size);
        }
    }
}

.label {
    color: vars.$color-type-base-contrast;
    font-size: utils.to-rem(16px);
}

.description {
    color: vars.$color-type-low-contrast;
    font-size: utils.to-rem(12px);
    padding: 8px 0 8px 40px;
}
