@use 'styles/utils' as utils;
@use 'styles/media' as media;
@use 'styles/variables' as vars;
@use 'styles/elements/inputs' as inputs;

.root {
    display: flex;
    position: relative;
    width: 100%;
}

.toggler {
    @include utils.set-size(20px);
    @include utils.set-transition(transform);

    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);
    pointer-events: none;
    color: currentColor;

    &.isRotated {
        transform: translateY(-50%) rotate(180deg);
    }
}

.label {
    @include inputs.label(true);

    &.isLabelFloated {
        @include inputs.floated-label;

        &.isError {
            color: vars.$color-semantic-error-500;
        }
    }
}

.titleContainer {
    @include utils.set-transition((color, border-color));

    background-color: vars.$color-foundation-surface;
    border: 1px solid vars.$color-components-input-groups-border;
    border-radius: 4px;
    color: vars.$color-type-base-contrast;
    cursor: pointer;
    padding: 8px 24px 8px 16px;
    height: 40px;
    width: 100%;

    @include media.no-touch-hover {
        border-color: vars.$color-primary-200;

        .label {
            &.isLabelFloated {
                color: vars.$color-primary-200;
            }
        }
    }

    &:disabled {
        background-color: vars.$color-components-input-groups-disabled-background;
        pointer-events: none;
    }

    &.isActive {
        border-color: vars.$color-primary-500;

        .label {
            &.isLabelFloated {
                color: vars.$color-primary-500;
            }
        }
    }

    &.isError {
        border-color: vars.$color-semantic-error-500;

        .label {
            &.isLabelFloated {
                color: vars.$color-semantic-error-500;
            }
        }
    }
}

.list {
    position: absolute;
    top: calc(100% + 8px);
    right: 0;
    z-index: vars.$z-index-select-list;
    box-shadow: 0 4px 6px 1px rgba(#334155, 0.3);
    border-radius: 4px;
    background-color: vars.$color-foundation-surface;
    padding-block: 8px;
    max-height: 216px;
    width: 100%;
    overflow-y: auto;

    &.isTopDirection {
        bottom: calc(100% + 8px);
        top: auto;
    }
}

.option {
    @include utils.set-transition((background-color, color));

    position: relative;
    padding: 8px 0 8px 28px;
    width: 100%;
    font-size: utils.to-rem(14px);

    &:disabled {
        color: vars.$color-primary-200;
    }

    &:not(:disabled) {
        @include media.no-touch-hover {
            background-color: vars.$color-primary-50;
            color: vars.$color-primary-500;
        }
    }
}

.subText {
    @include inputs.subtext;

    &.isError {
        @include inputs.subtext-error;
    }
}
