@use 'variables' as vars;

@mixin vw-small-down {
    @media (max-width: vars.$breakpoint-small - 1) {
        @content;
    }
}

@mixin vw-small-up {
    @media (min-width: vars.$breakpoint-small) {
        @content;
    }
}

@mixin vw-medium-down {
    @media (max-width: vars.$breakpoint-medium - 1) {
        @content;
    }
}

@mixin vw-medium-up {
    @media (min-width: vars.$breakpoint-medium) {
        @content;
    }
}

@mixin vw-medium-only {
    @media (min-width: vars.$breakpoint-small) and (max-width: vars.$breakpoint-medium - 1) {
        @content;
    }
}

@mixin vw-large-down {
    @media (max-width: vars.$breakpoint-large - 1) {
        @content;
    }
}

@mixin vw-large-up {
    @media (min-width: vars.$breakpoint-large) {
        @content;
    }
}

@mixin vw-large-only {
    @media (min-width: vars.$breakpoint-medium) and (max-width: vars.$breakpoint-large - 1) {
        @content;
    }
}

@mixin no-touch-hover {
    @media (hover: hover) and (pointer: fine) {
        &:hover {
            @content;
        }
    }
}
