@use 'styles/utils' as utils;
@use 'styles/media' as media;
@use 'styles/variables' as vars;

.root {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.chip {
    background-color: vars.$color-foundation-background;
    border-radius: 100px;
    align-items: center;
    display: flex;
    gap: 4px;
    padding: 4px 4px 4px 16px;

    &.isError {
        background: vars.$color-semantic-error-50;
    }
}

.label {
    font-size: utils.to-rem(14px);
    font-weight: 500;
}

.button {
    @include utils.set-size(32px);
    @include utils.set-transition(background-color);
    @include utils.flex-center;

    background-color: transparent;
    border-radius: 100%;
    color: vars.$color-primary-500;
    padding: 8px;

    @include media.no-touch-hover {
        background-color: vars.$color-100;
    }

    svg {
        flex-shrink: 0;
        width: 16px;
        height: 16px;
    }
}
