@use 'styles/utils' as utils;
@use 'styles/variables' as vars;

/* stylelint-disable */
.root {
    --yarl__color_backdrop: rgba(0, 11, 40, 0.8);
    --yarl__carousel_padding_px: 40;
    --yarl__thumbnails_thumbnail_background: transparent;
    --yarl__thumbnails_thumbnail_border_color: #{vars.$color-100};
    --yarl__thumbnails_thumbnail_active_border_color: #{vars.$color-400};
    --yarl__button_filter: none;

    backdrop-filter: blur(2px);

    :global(.yarl__slide_image) {
        border-radius: 4px;
    }

    :global(.yarl__thumbnails_container) {
        padding: 0 42px 16px;
    }
}

/* stylelint-enable */

.icon {
    @include utils.set-size(24px);

    color: white;

    &.isPrev {
        rotate: 90deg;
    }

    &.isNext {
        rotate: -90deg;
    }
}
